@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light+Two&display=swap);
*{
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    scroll-behavior: smooth;
}

.homepage {
    padding-top: 2vw;
    text-align: center;
}

.homepage h1 {
    font-size: 4vw;
    padding-bottom: 2vw;
    padding-left: 0;
    padding-right: 0;
    color: #1E56A7;
}

.homepage h2 {
    padding-top: 2vw;
    padding-bottom: 2vw;
    font-size: 2.5vw;
}

.homepage h3 {
    padding-top: 2vw;
    font-size: 2vw;
    font-weight: 600;
}

.image{
    width: 50vw;
    margin: 2vw auto;
}

.main-text {
    width: 60%;
    margin: auto;
    font-size: 1.6vw;
    font-weight: 600;
    margin-top: 1vw;
    padding-bottom: 2vw;
}

.diagram {
    margin-top: 1vw;
    border-radius: 1vw;
}

.card-holder{
    margin-top: 9vw;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 3vw;
    gap: 3vw;
    margin-bottom: 1vw;
}

.card {
    background-color: red;
    position: relative;
    width: 32vw;
    height: 13vw;
    text-align: start;
    padding-top: 1vw;
    padding-left: 1.5vw;
    padding-bottom: 1vw;
    border-radius: 1vw;
    color: #FCFCFF;
    box-shadow: 0px -2px 6px #0000005F;
}

.card button {
    padding: 0.8vw 2.7vw;
    border-radius: 0.5vw;
    border: none;
    margin-top: 1.2vw;
    font-size: 1.1vw;
    cursor: pointer;
    text-transform: uppercase;
    position: relative;
    bottom: 0;
    color: #0A122A;
    font-weight: 600;
}

.card button:hover {
    box-shadow: 0px -2px 6px #0000005F;
    bottom: 0.1vw;
}

.card-1 {
    background-color: #698F3F;
}


.card-2 {
    background-color: #804E49;
}

.card-heading {
    font-size: 2.3vw;
    font-weight: 600;
    margin-bottom: 1.5vw;
}

.card-text {
    width: 50%;
    font-size: 1.3vw;
}

.lady-1{
    width: 15vw;
    position: absolute;
    top: -7.8vw;
    right: 0;
}

.lady-2{
    width: 23vw;
    position: absolute;
    top: -8.6vw;
    right: -3vw;
}

.feature-holder {
    width: 55%;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: stretch;
    grid-gap: 1.5vw;
    gap: 1.5vw;
    margin-bottom: 2vw;
}

.feature {
    padding: 1.5vw 3vw;
    border-radius: 0.7vw;
    box-shadow: 0px -2px 6px #0000005F;
    color: #0A122A;
    /* width: 12%; */
    position: relative;
    bottom: 0;
    flex: 1 1;
    display: flex;
    flex-direction: column;
}

.feature-heading {
    font-size: 1.4vw;
    font-weight: 600;
}

.feature-text {
    margin-top: 1vw;
    font-size: 1vw;
    font-weight: 00;
}

.feature:hover{
    bottom: 0.1vw;
}

.footer {
    font-size: 1.2vw;
    margin-bottom: 2vw;
    font-weight: 500;
    color: #804E49;
}
.nav {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding-left: 10vw;
    padding-right: 10vw;
    padding-top: 1vw;
    padding-bottom: 1vw;
}


.nav-link {
    font-size: 4vw;
    font-family: 'Shadows Into Light Two', cursive;
    font-weight: 900;
    color: #1E56A7;
    text-decoration: none;
    text-shadow: 0px 1px 10px rgba(27,65,220,0.6);
}

.item {
    margin: auto 1.5vw;
}

.link {
    font-size: 1.2vw;
    color: #0a122a;
    font-weight: 500;
    position: relative;
    top: 0;
    text-decoration: none;
}

.link:hover{
    color: #1E56A7;
    top: -2px;
}

.item button {
    padding: 0.3vw 1.8vw;
    font-size: 1.2vw;
    color: #FCFCFF;
    font-weight: 500;
    border-radius: 0.3vw;
    background-color: #698f3f;
    border: none;
    cursor: pointer;
}

.item button:hover{
    background-color: #1E56A7;
}

.dropdown {
    position: absolute;
    right: 11.5vw;
    border-radius: 0.5vw;
    top: 7vw;
    background-color: #698f3f;
    padding: 1vw;
    color: #FCFCFF;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 0.7vw;
    gap: 0.7vw;
}

.mentor, .mentee {
    font-weight: 600;
    -webkit-text-decoration: underline #FCFCFF solid;
            text-decoration: underline #FCFCFF solid;
}

.drop {
    display: flex;
    grid-gap: 2vw;
    gap: 2vw;
    justify-content: center;
    align-items: center;
}

.drop div {
    padding: 0.4vw;
}

.sign-links:hover {
    -webkit-text-decoration: underline #FCFCFF solid;
            text-decoration: underline #FCFCFF solid;
    cursor: pointer;
}

.sign-links {
    color: #FCFCFF;
    text-decoration: none;
}

.dropdown-close {
    display: none;
}
.special-features {
    text-align: center;
}


.special-features .heading {
    font-size: 2.2vw;
    font-weight: 600;
    padding-bottom: 2vw;
}

.sp {
    padding-top: 3vw;
    font-weight: 600;
}

.features-holder {
    width: 80%;
    display: flex;
    padding-left: 3vw;
    padding-right: 3vw;
    /* padding-top: 1.8vw; */
    padding-bottom: 2vw;
    justify-content: center;
    align-items: center;
    margin: auto;
}

.holder-text {
    width: 60vw;
    padding: 1vw;
}

.holder-image {
    width: 40vw;
}

.special-features h2 {
    padding-bottom: 2vw;
    font-size: 2vw;
}

.text {
    font-size: 1.2vw;
    font-weight: 500;
}

.featuress-holder {
    display: flex;
    width: 80%;
    margin: auto;
    grid-gap: 1vw;
    gap: 1vw;
    justify-content: center;
    padding-bottom: 2vw;
}

.features {
    padding: 1.6vw 1vw;
    flex: 1 1;
    border-radius: 1vw;
    bottom: 0;
    position: relative;
}

.features:hover{
    bottom: 0.1vw;
}

.features-heading {
    font-weight: 600;
    padding-bottom: 1vw;
}

.features-text {
    font-weight: 500;
    width: 90%;
    margin: auto;
}
.members {
    padding-top: 3vw;
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 5vw;
    gap: 5vw;
    padding-bottom: 2vw;
}

.heading {
    text-align: center;
    font-weight: 500;
    font-size: 3vw;
    padding-top: 2vw;
}

.member {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 0.3vw;
    gap: 0.3vw;
    background-image: url(/Coach-Me/static/media/blobBg.9ccf3ccc.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.img {
    width: 4vw;
}


.socials img {
    width: 1.5vw;
}

.socials {
    display: flex;
    justify-content: center;
    align-items: center;
    grid-gap: 0.6vw;
    gap: 0.6vw;
    padding-top: 0.5vw;
}

.member .name {
    font-weight: 500;
    font-size: 1.5vw;
}

.member .position {
    font-weight: 500;
    font-size: 1.2vw;
}

.text {
    text-align: center;    
    font-weight: 500;
    padding: 3vw 12vw;
}

.text span a{
    color: #B9D8DB;
    text-decoration: none;
}


.text .subtext {
    line-height: 5vw;
}

.contact-us-text {
    font-size: 3.5vw;
    padding-top: 2vw;
    text-align: center;
    font-weight: 700;
    color: #528681;
}

.contact-us img {
    width: 30vw;
}

.holder {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 80%;
    margin: auto;
    padding-left: 5vw;
    padding-top: 2vw;
}

.holder .form {
    padding-left: 3vw;
    padding-right: 3vw;
    width: 30vw !important;
    border-radius: 1vw;
}

.input-holder {
    display: flex;
    flex-direction: column;
    grid-gap: 0.8vw;
    gap: 0.8vw;
    padding: 0.6vw ;
    font-weight: 500;
}

textarea {
    resize: none;
    outline: none;
    padding: 0.5vw;
    border-radius: 0.5vw;
}

input {
    /* width: 70%; */
    outline: none;
    padding: 0.5vw;
    border-radius: 0.5vw;
    border-width: thin;
}

.button {
    padding-bottom: 1vw;
}

.contact-us button {
    width: 100%;    
    height: 3vw;
    font-size: 1.2vw;
    font-weight: 500;
    background-color: #FF9C85;
    border: none;
    color: #fff;
    text-transform: uppercase;
    border-radius: 0.2vw;
    cursor: pointer;
}

